.mouse-over-screen {
	position: absolute;
	inset: 0;
	z-index: 100;
	opacity: 0.1;
	mix-blend-mode: screen;
	transition-duration: 150ms;

	&:hover {
		opacity: 0.5;
	}
}

.full-width-video {
	width: 100%;
}

.modern-portfolio {
	--base-duration: .2s;
	--long-duration: 1.5s;
	--very-long-duration: 5s;
	display: flex;
	flex-direction: column;
	justify-content: stretch;
	
	position: relative;
	flex-basis: 100%;
	//padding-inline: 2rem;
	overflow: hidden;
	isolation: isolate;

	&--background {
		position: absolute;
		inset: 0;
		z-index: -1;
		//background-color: #111;

		img {
			object-fit: cover;
			min-width: 100%;
			min-height: 100%;
			object-position: center;
			opacity: 0;
			filter: contrast(0.8) brightness(1.5) opacity(0.125);
		}

		@container (width > 64em) {
			background-color: #000;

			img {
				opacity: 1;
			}
		}
	}


	&--entry {
		background-color: #070707;
		padding: 1rem 1rem 3rem 1rem;
		margin: .5rem;
		
		
		display: flex;
		flex-direction: column-reverse;
	
		gap: 1.5rem;
		box-shadow: 0 0 0.5rem 0rem rgba(0, 0, 0, 0.25);
		position: relative;


		&:hover {
			.media {
				img {
					box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.5);
					transform: scale(1.02);
				}
			}

			.discover {
				background-color: $white;
				color: $offblack;
			}

			.engine-icon {
				svg {
					opacity:1;
				}
			}
		}

		@container (width > 64em) {
			flex-direction: row;
			padding: 1rem;
			padding-left: 6rem;
			gap: 3rem;
			max-width: 1600px;
			margin: 12rem auto;
			min-height: 50vh;
		}

		h1 {
			text-transform: uppercase;
			letter-spacing: 0.5rem;
			font-size: clamp(2rem, 3vw, 2.5rem);
			line-height: 1.5;
			font-weight: 400;
			color: $white;
			text-align: center;
			//word-wrap: break-word;

			&:last-of-type {
				margin-bottom: 0rem;
			}

			@container (width > 64em) {
				font-size: 3.5rem;
				line-height: 4rem;
			}
		}

		h2 {
			text-transform: uppercase;
			letter-spacing: 0.25rem;
			font-size: clamp(1.25rem, 2vw, 1.5rem);		
			font-weight: 300;
			line-height: 1.5;
			color: $lightergrey;
			text-align: center;
			word-wrap: break-word;

			&:last-of-type {
				margin-bottom: 2rem;
			}

			@container (width > 64em) {
				font-size: 2.5rem;
				line-height: 3rem;
			}
		}

		p {
			font-size: 1.25rem;
			line-height: 1.5;
			font-weight: 300;
			color: $lightgrey;

			&:last-of-type {
				margin-bottom: 0rem;
			}

			@container (width > 64em) {
				font-size: 1.4rem;
				line-height: 2rem;
			}
		}

		.content {
			flex: 50% 0 0;
			display: flex;
			flex-direction: column;
			justify-content: center;
			position: relative;
			padding: 1rem;

			@container (width > 64em) {
				&:after {
					content: "";

					position: absolute;
					inset: -3rem -2rem -1rem -2rem;
					z-index: 2;
					pointer-events: none;
					border: 3px solid white;
					transform: scale(1.05);

					@container (width > 64em) {
						inset: -4rem -8rem -4rem -3.5rem;
					}
				}
			}
		}

		.media {
			flex: 100% 1 1;
			align-self: stretch;
			justify-self: stretch;
			display: flex;
			overflow: hidden;
			transition-duration: var(--base-duration);

			img {
				min-width: 100%;
				min-height: 100%;
				object-fit: cover;
				object-position: center;
				aspect-ratio: 1.667;
				
				flex-shrink: 0;
				
				box-shadow: none;
				transition-duration: var(--base-duration);

			}
		}

		.discover {
			font-size: 1rem;
			line-height: 1.5rem;
			transition: var(--base-duration) cubic-bezier(0.65, 0, 0.35, 1);
			align-self: flex-start;
			justify-self: flex-start;
			cursor: pointer;
			opacity: 1;
			padding: 0.5rem 1rem;
			margin-top: 3rem;

			background-color: rgba(255, 255, 255, 0);
			color: $white;


			&:hover,
			&:active {
				background-color: $white;
				color: $offblack;
			}
		}
	}

	@container (width > 64em) {
		&--focused {
			.media {
				img {
					transition-duration: var(--very-long-duration);
					transform: scale(1.1);

				}
			}

			.content {
				&::after {
					transition-duration: var(--long-duration);
					transform: scale(1);
					border-color: white;
				}
			}
		}
	}

	.tags {
		margin-top: 1.5rem;
		display: flex;
		flex-direction: row;
		gap: 0.75rem;
		flex-wrap: wrap;

		p {
			font-size: 1.1rem;
			display: inline-block;
			margin: 0;
			padding: 0.25rem 1rem;
			background-color: #151515;
			border-radius: 0.5rem;

			&:before {
				content: "•";
				margin-right: 0.5rem;
				color: $midgrey;

			}
		}
	}

	.engine-icon {
		position: absolute;
		top: 0;
		right: 0;
		background-color: #070707;
		padding: 1rem 1rem 0.75rem 0.75rem;

		svg {
			$iconSize:4rem;
			fill: white;
			width: $iconSize;
			height: $iconSize;
			opacity:0.75;
			transition-duration: var(--base-duration);
		}
	}
}

.modern-projects {
	display: grid;
	grid-template-columns: repeat(1, 1fr);


	@include container-between(medium, large) {
		grid-template-columns: repeat(2, 1fr);
	}

	@include container-up(xlarge) {
		grid-template-columns: repeat(4, 1fr);
	}


}

.featured-project {
	container-type: inline-size;
	position: relative;

	>.modern-portfolio {
		
		padding-top: 12rem;
		padding-bottom: 2rem;

		@include breakpoint-up(xlarge) {
			grid-template-columns: repeat(3, 1fr);
			padding-block: 12rem;
		}

		
		
	}
}
.floating-title, .solid-title {

	
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	justify-items: center;
	gap: 1rem;

	color: white;
	

	svg {
		fill: $lightgrey;
		width: 4rem;
		height: 4rem;
	}

	h2 {
		font-size:1.2rem;
		@include breakpoint-up(large) {
			font-size:1.5rem;
		}

		font-weight: 300;
		padding-top: 0.2rem;
		color:$white;

		&:last-of-type {
			margin: 0; 
		}
	}
}

.floating-title {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	top: 4rem;
	
	z-index: 2;

}
.solid-title {
	padding-block: 4rem;	
}

.latest-projects {
	background-color: #101010;
}

.projects {
	position: relative;
	padding-bottom: 4rem;
	display:grid;
	grid-template-columns: repeat(1, 1fr);
	@include breakpoint-up(large) {
		grid-template-columns: repeat(2, 1fr);
	}

	&--landing {
		padding-inline: 1rem;

		@include breakpoint-up(large) {
			grid-template-columns: repeat(2, 1fr);
			padding-inline: 4rem;
		}
		@include breakpoint-up(xlarge) {
			grid-template-columns: repeat(4, 1fr);
			padding-inline: 6rem;
		}
	}
}