h1, h2, h3, h4, h5, h6{
    margin:0;
}

h1 {
	text-transform: uppercase;
	font-size: 1.5rem;
	letter-spacing: .5rem;
	margin-bottom: 0.5rem;
	line-height:2rem;
}

h2 {
	font-size: 1.15rem;
	text-transform: uppercase;
	letter-spacing: 0.5rem;
	color: $midgrey;
	margin-bottom: 0.75rem;
	line-height:1.5rem;
}

i {
	color:$lightergrey;
	margin-right:0.1rem;
}

p {
	line-height: 1.5rem;
}

strong {
    font-weight:700;
    color: $white;
}

blockquote {
    padding:1rem;
    border-left:5px solid $dsblue;
    margin:0;
    margin-bottom:0.75rem;
    background-color:$darkergrey;
	line-height:1.5rem;

    >p:last-child {
        margin-bottom:0;
    }
}

blockquote.large {
	margin-top:1rem;
	margin-bottom:2rem;
	padding:2rem;
	font-size:1.2rem;
	line-height: 2;
	line-height:3rem;
}


.weight-600 {
	font-weight:600;
}


.weight-700 {
	font-weight:700;
}

.weight-800 {
	font-weight:800;
}


.weight-200 {
	font-weight:200;
}

.fill-img {
	height:100%;
	width:100%;
	object-fit: cover;
	object-position: center;
}