@import "mixins";
@import "colors";
@import "mission-statement";

.services {

    &>.container {
        max-width: 1900px;
        display: grid;
        @include breakpoint-up(xlarge) {
            grid-template-columns: repeat(4, 1fr);
        }
		@include breakpoint-between(medium,large){
			grid-template-columns: repeat(2, 1fr);
		}

        grid-template-columns: repeat(1, 1fr);
        gap: 2rem;
    }

    background-color:#050505;
    padding-block:4rem;

   

    .services-entry {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        text-align: center;

        svg {
            $iconSize:3.5rem;

            width: $iconSize;
            height: $iconSize;
            fill: white;
            margin-bottom: 1rem;
			@include breakpoint-up(large) {
				margin-bottom: 2rem;
			}
          //  background-color: pink;
        }

        h2 {
			font-size:1.5rem;
            color: white;
            margin-bottom:1.5rem;
        }

        h3{
            font-size: 1.1rem; 
            color: $midgrey;
			font-weight: 400;

			@include breakpoint-up(large) {
				font-size: 1.25rem;
			}
        }

    }
}