@import "mixins";
@import "colors";
@import "project";

@font-face {
	font-family: 'FordAntenna';
	src: url('/fonts/ford-antenna-regular.woff2') format('woff2');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'FordAntenna';
	src: url('/fonts/ford-antenna-light.woff') format('woff');
	font-weight: light;
	font-style: normal;
}

@font-face {
	font-family: 'FordAntenna';
	src: url('/fonts/ford-antenna-semibold.woff2') format('woff2');
	font-weight: 600;
	font-style: normal;
}

@font-face {
	font-family: 'FordAntenna';
	src: url('/fonts/ford-antenna-bold.woff2') format('woff2');
	font-weight: bold;
	font-style: normal;
}

.ford,
.ford h1,
.ford h2,
.ford p {
	font-family: "FordAntenna";
	text-transform: none;
	letter-spacing: normal;
	font-weight: 400;	
}

