.services-nav {
	display: flex;
	flex-direction: column;
	gap: 4rem;

	align-items: center;
	justify-content: center;
	min-height: 100vh;

	font-size: 1.5rem;
	text-transform: uppercase;
	letter-spacing: 0.75rem;
	font-weight: 300;
	color: $midgrey;
}

header {
	.container {
		display: flex;
		flex-wrap: row;
		justify-content: space-between;
		align-items: center;
	}

	position:fixed;
	z-index: 99;
	padding:1rem 1rem;

	// @include breakpoint-up(xlarge) {
	// 	padding: 0.5rem 0;
	// }

	width:100vw;
	background-color:$offblack;

	.logo-area {
		align-self: flex-start;		
		cursor: pointer;
		margin:0;
		padding:0;
		//height:auto;
		max-width:200px;

		.logo {
			display:block;
			max-height: 2.5rem;	
			max-width:200px;

			@include breakpoint-down(medium) {
				display: none;
			}
		}

		.icon {
			max-width: 2rem;
			max-height: 2rem;

			@include breakpoint-up(large) {
				display: none;
			}
		}

		svg {			
			fill: white;
			
			
		}
	}

	nav {
		flex: 0 0 100px;
		flex-wrap: nowrap;
		display: flex;
		justify-content: space-between;
		font-size: 0.75rem;
		text-transform: uppercase;
		letter-spacing: 0.15rem;
		font-weight: 400;
		color: $midgrey;
		flex-direction: row;
		gap: 2rem;
		padding-inline:2rem;

		@include breakpoint-up(medium) {
			gap: 4rem;
			
			
		}
	}
}

.header-spacer {
	min-height: 3rem;
}