@import url('https://fonts.googleapis.com/css2?family=Zilla+Slab:wght@300;400;700&display=swap');

::selection {
	background-color: #C3CFE2;
}

.blur-container {	
	backdrop-filter: brightness(20%);
	position: absolute;
	inset: 0;
	display: flex;
	align-items: center;
	justify-content: center;
}

.fill-noise {
	position: absolute;
	inset:0;
	background-image: url("../assets/Noise.png");
	opacity:0.5;
	mix-blend-mode:soft-light;
	z-index:1;
}

.mission-statement {
	min-height: 100vh;
	position: relative;

	.background {
		position: absolute;
		inset:0;
		min-width: 100%;
		min-height: 100%;
		object-fit: cover;
		object-position: center;
		z-index: -1;
	}

	.content-box {
		flex-direction:column;
		position: relative;

		@include breakpoint-up(medium) {
			flex-direction:row;
		}

		@include breakpoint-up(large) {
			width: 45%;
		}

		gap:4rem;		
		padding: 4rem;

		@include breakpoint-up(large) {
			
		
		&:after {
			content: "";
			position: absolute;
			inset: -3rem -2rem -3rem -2rem;
			z-index: 2;
			pointer-events: none;
			border: 3px solid white;		
		}
	}
	}

	strong {
		color: #fff;
		font-weight: 800;
	}

	.logo {
		$size: clamp(5rem, 8vw, 8rem);
		flex: 0 0 $size;
		fill: $white;
		max-height: $size;
		max-width: $size;
	}

	.right {
		//border-left:2px solid $midgrey; 
		@include breakpoint-up(medium) {
			padding-left: 4rem;
		}
		padding-left: 1rem;
	}
	
	line-height: 1.35;
	letter-spacing: normal;
	// font-family: 'Zilla Slab',serif;
	color: $lightergrey;

	h1 {
		font-weight: 300;
		font-size: 1.25rem;
		line-height:4rem;

		@include breakpoint-up(medium) {
			font-size: 1.5rem;
		}
	
		@include breakpoint-up(xlarge) {
			font-size: 2.5rem;
		}
	}
}

.text-js {
	opacity: 1;
	display: block;
	margin: 0;
}


.cursor {
	display: block;
	position: absolute;
	height: 100%;
	top: 0;
	right: -5px;
	width: 2px;
	/* Change colour of Cursor Here */
	background-color: white;
	z-index: 1;
	animation: flash 0.5s none infinite alternate;
}

@keyframes flash {
	0% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
}

