@import "mixins";
@import "colors";

.brief {

	text-align: center;
	color: $lightergrey;
	padding: 8rem 2rem;
	font-weight:300;
	
	

	@include breakpoint-up(medium) {
		padding: 8rem 6rem;
	}

	@include breakpoint-up(large) {
		padding: 8rem 0;
	}

	strong {
		font-weight: 800;		
	}

	h1 {
		font-size: 1rem;
		line-height:1.75rem;

		@include breakpoint-up(medium) {
			font-size: 2rem;
			line-height:3rem;
		}
	}

	p {
		font-size: 1.2rem;
		line-height:3rem;

		@include breakpoint-up(medium) {
			font-size: 2.2rem;
		}
	}

	.button {

		text-transform: uppercase;
		font-weight: 700;
		transform: translateX(-1rem);
		margin-top: 0.5rem;

		@include breakpoint-up(large) {
			font-size: 1.2rem;
			padding-left: 2rem;
			transform: translateX(-2rem);
		}


		float:left;
		padding:1rem;
		color:$lightgrey;
		display:flex;
		align-items: center;

		transition-timing-function: ease-in-out;
		transition-duration: 0.15s;

		cursor:pointer;

		svg {
			fill: $white;
			max-height: 3rem;
			max-width: 3rem;
			padding-left: 1rem;
		}

		&:hover {
			color: $white;

			&>span {
				margin-left: 0.75rem;
				color: $dsblue;
			}
		}

		>span {
			margin-left: 0.5rem;

			transform: scale(1.5);
			transition-timing-function: ease-in-out;
			transition-duration: 0.15s;
		}
	}

}

.full-width {
	width: 100%;
	background-color: #ccc;
}

.secondary-images {
	span {
		font-size: 0px;
		display: inline-block;
	}
}


.full-height {

	@include breakpoint-up(medium) {
		height: 100vh;
	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		max-height: 100vh;
	}


	&-title {

		color: #bbbbbb;
		padding: 4rem 2rem;
		backdrop-filter: blur(0.15rem);
		background-color: rgba(0, 0, 0, 0.25);


		@include breakpoint-up(medium) {
			backdrop-filter: blur(0.25rem);
			position: absolute;
			max-width: 85vw;

		}

		@include breakpoint-up(large) {
			backdrop-filter: none;
			background-color: rgba(0, 0, 0, 0);
			padding: 0rem;

		}

		@include breakpoint-up(xlarge) {
			max-width: 35vw;
		}


		text-align: center;

		&--right {
			@include breakpoint-up(medium) {
				text-align: right;
				bottom: 0;
				right: 0;
				left: auto;

				@include breakpoint-up(xlarge) {
					top: 25vh;
					bottom: auto;
				}

				.feature {
					justify-content: flex-end !important;
				}
			}

			@include breakpoint-up(large) {
				right: 5vw;
			}


		}

		&--left {
			@include breakpoint-up(medium) {
				text-align: left;
				bottom: 0;
				left: 0rem;
				right: auto;

				@include breakpoint-up(xlarge) {
					bottom: 15vh;
					top: auto;
				}

				.feature {
					justify-content: flex-start !important;
				}
			}

			@include breakpoint-up(large) {
				left: 5vw;


			}


		}

		h1 {
			&:last-of-type {
				margin-bottom: 1rem;
			}

			color: $white;
			letter-spacing: normal;

			font-size: 2rem;
			line-height: 2.5rem;

			@include breakpoint-up(medium) {
				font-size: 4rem;
				line-height: 4.5rem;
			}
		}

		h2 {
			color: $white;
			text-transform: none;
			letter-spacing: normal;
			font-weight: 200;

			font-size: 1.25rem;
			line-height: 1.5rem;

			@include breakpoint-up(medium) {
				font-size: 2.5rem;
				line-height: 2.5rem;
			}

			&:last-of-type {
				margin-bottom: 1rem;

				@include breakpoint-up(medium) {
					margin-bottom: 3rem;
				}
			}
		}

		p {
			font-size: 1.15rem;

			@include breakpoint-up(medium) {
				font-size: 1.45rem;
			}
		}

		.icon {
			max-width: 2rem;
			max-height: 2rem;
			fill: $lightgrey;
			display: inline;
		}

		.feature {
			//flex-grow: 1;
			gap: 1rem;
			justify-content: center;

			color: $lightergrey;
			text-align: center;

			@include breakpoint-up(medium) {
				text-align: left;
			}

			flex-direction: row;
		}
	}
}


.wide-header {
	background-image: linear-gradient(#0a0a0a, #0e0e0e);
	display: flex;
	align-items: center;
	justify-content: center;
	position: relative;
	height: 100vh;

	&-title {
		position: absolute;
		text-align: center;
		bottom: 3rem;

		@include breakpoint-up(medium) {
			bottom: 6rem;
		}

	}

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
	}

	h1 {
		color: $white;
		font-weight: 100;
		letter-spacing: normal;
		text-transform: none;

		font-size: 2rem;
		line-height: 2.5rem;

		@include breakpoint-up(medium) {
			font-size: 4rem;
			line-height: 4.5rem;
		}
	}



	h2 {
		font-size: 2rem;
		color: $lightergrey;

		font-weight: 100;
		letter-spacing: normal;
		text-transform: none;

		font-size: 1rem;
		line-height: 1.5rem;

		@include breakpoint-up(medium) {
			font-size: 2rem;
			line-height: 2.5rem;
		}
	}
}

.featured-full-width {

	width: 100%;
	height: 100vh;
	overflow: hidden;
	position: relative;
	

	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		max-height: 100vh;
		
	}

	.latest-title {
		position: absolute;
		top: 2rem;
		right: 2rem;

		display: flex;
		align-items: center;
		gap: 1rem;
		font-size: 1.75rem;
		color: $lightergrey;

		.icon {
			width: 3rem;
			height: 3rem;
			fill: $lightergrey;
		}


	}

	.featured-title {
		position: absolute;
		bottom: 4rem;
		left: 0;

		color: white;
		text-align: left;
		padding: 1rem 2rem;

		@include breakpoint-up(medium) {
			left: 2rem;
		}

		h1 {
			font-size: 4rem;
			line-height: 4.5rem;
		}

		h2 {
			font-size: 2.5rem;
			color: $lightergrey;
		}

		.button {

			text-transform: uppercase;
			font-weight: 700;
			transform: translateX(-1rem);
			margin-top: 0.5rem;

			@include breakpoint-up(large) {
				font-size: 1.2rem;
				padding-left: 2rem;
				transform: translateX(-2rem);
			}


			float:left;
			padding:1rem;
			color:$lightgrey;
			display:flex;
			align-items: center;

			transition-timing-function: ease-in-out;
			transition-duration: 0.15s;

			cursor:pointer;

			svg {
				fill: $white;
				max-height: 3rem;
				max-width: 3rem;
				padding-left: 1rem;
			}

			&:hover {
				color: $white;

				&>span {
					margin-left: 0.75rem;
					color: $dsblue;
				}
			}

			>span {
				margin-left: 0.5rem;

				transform: scale(1.5);
				transition-timing-function: ease-in-out;
				transition-duration: 0.15s;
			}
		}

	}
}