$default-shadow: 0.05rem 0.05rem 0.5rem rgba(0, 0, 0, 0.3);
$blur-amount: 8.5rem;

.img-show {
	//background: black;
	$max-scale: 115%;
	overflow: hidden;
	position: absolute;
	inset:0;
	z-index:-1;


	img {
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center center;
		overflow: hidden;
	}

	.entry {
		position: absolute;
		inset: 0;
		opacity: 0;
		transform: scale($max-scale);
		transition: opacity 500ms linear;
		pointer-events: none;
		filter:blur($blur-amount);
	}

	.entry.active {
		opacity: 1;
		
		animation-name: gallery-transition;
		animation-duration: var(--animation-duration);
		
		/* Adjust duration as needed */
		animation-timing-function: cubic-bezier(0.61, 1, 0.88, 1);
		animation-iteration-count: 1;
		pointer-events: inherit;
	}

	@keyframes gallery-transition {
		0% {
			/* Initial state */
			transform: scale(100%);
			filter:blur($blur-amount) brightness(0.2);
		}

		10% {
			filter:blur(0) brightness(1);
		}

		70% {
			/* Transition to final state */
			transform: scale($max-scale);
			filter:blur(0) brightness(1);
		}

		90% {
			filter:blur(0) brightness(1);
		}

		100% {
			/* Remain at final state */
			transform: scale($max-scale);
			filter:blur($blur-amount) brightness(0.2);
		}
	}
}