footer {
	background-color: $offblack;
}

footer .container { 
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	padding: 4rem 0;
	font-weight: 300;
	font-size: 1rem;
	margin-top: 0;
	margin-bottom: 0;
	gap: 2rem;

	@include breakpoint-up(large) {
		flex-direction: row;
	}

	svg {
		max-height: 2rem;
		max-width: 2rem;
		fill: $white;
	}

	p>span {
		display: block;
	}

	strong {
		font-weight: 700;
	}

	.col-2 {
		@include breakpoint-up(large) {
			text-align: left;
		}
	}

	
	.right {
		display: flex;
		flex-direction: column;
		align-items: center;
		row-gap: 1rem;

		@include breakpoint-up(large) {
			align-items: flex-end;
		}
		
		svg {
			max-width: 1.5rem;
			max-height: 1.5rem;
			transition-timing-function: ease-in-out;
			transition-duration: 0.15s;
		}
	}

	.email:hover{
		svg {			
			fill: $dsblue;
		}
	}

	.social {
		gap:0.5rem;

		svg {
			transition-timing-function: ease-in-out;
			transition-duration: 0.15s;
		}

		a:hover svg {
			fill: $dsblue;
		}
	}
}