.flex {
	display: flex;
	align-items: center;
	flex-direction: column;
	gap: 3rem;

	@include breakpoint-up(large) {
		flex-direction: row;
	}

	&-right {
		justify-content: end;
	}
}

.flex-wrap {
	flex-wrap: wrap;
}

.flex-align-top {
	align-items: flex-start;
}

.flex-align-str {
	align-items: stretch;
}



.flex-icon-row {
	display:flex;
	align-items: center;

	flex-direction: row;
	gap:1rem;

	p {
		margin-bottom:0;
	}
}

.flex-column {
	display:flex;
	flex-direction: column;
	gap: 1rem;
}

.col {
	flex-basis: 0;
	flex-grow: 1;
	flex-shrink: 1;
}

.col-2 {
	flex-basis: 50%;
	flex-grow: 1;
	flex-shrink: 1;
}

// Gap Helpers

.gap-00 {
	gap:0;
}

.gap-05 {
	gap: 0.5rem;
}

.gap-10 {
	gap: 1rem;
}

.gap-15 {
	gap: 1.5rem;
}

.gap-20 {
	gap: 2.0rem;
}

.gap-25 {
	gap: 2.5rem;
}

.gap-30 {
	gap: 3.0rem;
}

.gap-00-mobile {
	@include breakpoint-down(medium) {
		gap: 0;
	}
}

// Shrink Helpers

.shrink-00 {
	flex-shrink: 0.0;
}

.shrink-05 {
	flex-shrink: 0.5;
}

.shrink-10 {
	flex-shrink: 10;
}

.shrink-15 {
	flex-shrink: 1.5;
}

.shrink-20 {
	flex-shrink: 2.0;
}

.shrink-25 {
	flex-shrink: 2.5;
}

// Grow Helpers

.grow-00 {
	flex-grow: 0.0;
}

.grow-05 {
	flex-grow: 0.5;
}

.grow-10 {
	flex-grow: 1.0;
}

.grow-15 {
	flex-grow: 1.5;
}

.grow-20 {
	flex-grow: 2.0;
}

.grow-25 {
	flex-grow: 2.5;
}

.grow-30 {
	flex-grow: 3.0;
}